import React, { useState } from "react";
import ReactDOM from "react-dom";
import logo from "../imgs/logo.jpeg";

import Home from './home';
import About from './about';
import ContactUs from './contactus';
import Report1 from './report1';
import Report2 from './report2';

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
  //NavbarText
} from "reactstrap";

const Header = props => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  
  const dist = document.querySelector("#bodyContainer");
  const homePage = ()=> {
    //ReactDOM.render(<Body />,document.querySelector("#bodyContainer"));
  }

  const aboutPage = ()=> {
    ReactDOM.render(<aboutPage />,dist)
  }

  const contactusPage = ()=> {
    ReactDOM.render(<ContactUs />,dist)
  }

  return (
    <div id="headerContainer">
      <img id="logo" src={logo} alt="" />
      <div id="NavBarContainer">
        <Navbar
          style={{ backgroundColor: "#333" }}
          //color="light"
          light
          expand="md"
        >
          <NavbarBrand 
          style={{ color: "#fff" }} href="/"
          onClick ={()=>ReactDOM.render(<Home />,document.querySelector("#bodyContainer"))}
          >
            الرئيسية
          </NavbarBrand>
          <NavbarToggler onClick={toggle} />
          <Collapse isOpen={isOpen} navbar>
            <Nav className="" navbar>
              <NavItem>
                <NavLink 
                style={{ color: "#fff", cursor: "pointer" }}
                onClick ={()=>ReactDOM.render(<About />,document.querySelector("#bodyContainer"))}
                
                >
                  عن الميدان
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ color: "#fff", cursor: "pointer" }}
                  onClick ={()=>ReactDOM.render(<ContactUs />,document.querySelector("#bodyContainer"))}
                >
                  تواصل معنا
                </NavLink>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle style={{ color: "#fff" }} nav caret>
                  الخدمات
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report1 />,document.querySelector("#bodyContainer"))}
                  >
                    تقرير1
                  </DropdownItem>
                  <DropdownItem
                    onClick ={()=>ReactDOM.render(<Report2 />,document.querySelector("#bodyContainer"))}
                  >
                    تقرير 2
                  </DropdownItem>
                  <DropdownItem divider />
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    </div>
  );
};

export default Header;